body{
  background-color: #1A1A40;
  color: white;
  font-size: calc(10px + 2vmin);
}

.App > div{
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.masteryCard{background-color: #2a303b; width:20%; margin: 1%;display:flex; flex-direction: column; justify-content: flex-start; text-align: left; border-radius: 5px;}
.masteryCard img{width: 100%;} 
.masteryCard div{padding: 0 1em;}

@media screen and (max-width: 800px){
  .masteryCard{width: 100%;}
}